* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  overflow-x: hidden;
}

#movies img, #movie img {
  width: 100%;
}

.brand-text {
  font-size: 2rem;
}

#react-logo {
  color: #00d8ff;
}

#imdb-logo {
  color: #f5de50;
}

@media (min-width: 960px) {
  #movies .col-md-3 .well {
    height: 390px;
  }
  #movies .col-md-3 img {
    height: 240px;
  }
}
